body {
  background-color: rgb(10, 150, 180);
}
#meterBox {
  width: 202px;
  margin: auto;
  text-align: center;
  border-radius: 2px;
  padding: 8px;
}
#wheel {
  margin: 10px auto 0;
}
#winBox {
  width: 10em;
  text-align: center;
  padding: 0.5em;
  background-color: white;
  margin: 0.5em auto;
}
button {
  padding: 0.5em 1em;
  margin-bottom: 1em;
  background-color: lawngreen;
  font-size: 16px;
  color: blue;
}
wheel {
  width: 100px;
  height: 270px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
whoption {
  width: 100px;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  overflow: hidden;
  transform: rotateX(0deg);
  transition-timing-function: linear;
}
whoption:first-child {
  min-height: 0px;
  height: 0;
}
whoption:nth-child(2) {
  min-height: 30px;
}
whoption:nth-child(3) {
  min-height: 60px;
}
whoption:nth-child(4) {
  min-height: 90px;
}
whoption:nth-child(5) {
  min-height: 60px;
}
whoption:nth-child(6) {
  min-height: 30px;
}
whoption:last-child {
  min-height: 0px;
  height: 0;
  display: none;
}

whoption p {
  transition-duration: 500ms;
  transform: rotateX(0deg);
  border: 1px solid black;
}
whoption:first-child p {
  transform: rotateX(90deg);
}
whoption:nth-child(2) p {
  transform: rotateX(60deg);
}
whoption:nth-child(3) p {
  transform: rotateX(30deg);
}
whoption:nth-child(4) p {
  transform: rotateX(0deg);
}
whoption:nth-child(5) p {
  transform: rotateX(-30deg);
}
whoption:nth-child(6) p {
  transform: rotateX(-60deg);
}
whoption:nth-child(7) p {
  transform: rotateX(-90deg);
}
